.card{
  position: absolute;
  z-index: 1000;
  width:fit-content;
  bottom: 120px;
  padding: 10px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  color: #ffffff;
  text-decoration: none;
  gap: 10px;
  background-color:rgba(35, 35, 37);
  &__img {
    border-radius: 16px;
  }
  &__tag {
    font-size: 16px;
  }
  &__eda {
    font-size: 12px;
    color: rgba($color: #ffffff, $alpha: 0.45);
    text-decoration: none;
    height: 16px;
  }
}