.button {
  position: absolute;
  z-index: 1000;
  height: 60px;
  width: 60px;
  bottom: 30px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #efefef;
  border-radius: 26px;
  border: 2px solid rgb(99, 55, 255);

  &:active{
    background-color: #dddddd;

  }
}